@import "../../node_modules/bootstrap/dist/css/bootstrap.css";

body,
html {
  height: 100%;
}
.bg {
  /* The image used */
  background-image: url(https://images.pexels.com/photos/772665/pexels-photo-772665.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);

  /* Full height */
  height: 50vh;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  position: relative;
}

.space {
  padding-top: 10%;
}

.react-datepicker__input-container {
  width: inherit;
}

.react-datepicker-wrapper {
  width: 100%;
}

#div2 {
  position: absolute;
  left: 50%;
  top: 50%;

  transform: translate(-50%, -50%);
}

.form-group {
  margin-bottom: auto;
}
