.hr-text {
  line-height: 1em;
  position: relative;
  outline: 0;
  border: 0;
  color: black;
  text-align: center;
  height: 1.5em;
  opacity: 0.5;
  margin: 5px;
}
.hr-text:before {
  content: "";
  background: red;
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 1px;
}

.cont-hr {
  margin: 40px auto 0 auto;
  max-width: 50%;
  font-size: x-large;
}

.content {
   min-height:100%;
   position:relative;
}

html {
  height: 100%; 
 }